.component-wrapper {
  margin-bottom: 30px;
  padding: 0 25px;
}
.component-heading {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 40px;
}
.component-heading h2 {
  font-size: 17px;
  margin: 0;
  font-weight: 600;
  color: hsl(292, 42%, 14%);
  cursor: pointer;
}
.component-heading h2:hover {
  color: #ad28eb;
}
.paragraph {
  font-size: 16px;
  font-weight: 00;
  color: hsl(292, 16%, 49%);
}
